export const initState = {
  pagination: {
    current: 1,
    defaultCurrent: 1,
    pageSize: 20,
    defaultPageSize: 20,
    total: 0,
    showTotal: (total, range) => ` ${range[0]} - ${range[1]}  ${total} `,
  },
  dataSource: [],
  modalTitle: '',
  modalVisible: false,
  record: {},
  index: null,
  search: {},
};



